import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Link, useLocation } from "react-router-dom";
import { menuStructure } from "../menuStructure";
import React from 'react';


export default function TopNavBar() {

    const location = useLocation()

    console.log("-------------------------------", location.pathname)

    const links = menuStructure.map((item, i) => {

        const active = (location.pathname.includes(item.route ?? "asdf") && item.route !== "/") || item.route === location.pathname

        console.log("item",   active, item.route)

        if (item.route) {

            return <Nav.Link as={Link} to={item.route} key={i} eventKey={i} 
                    active={false}
                    // className={}
                className={active ? "customActive1" : ""}

            >
                {/* <Link to="/impressum">Impressum</Link> */}
                {item.title}
            </Nav.Link>
        } else {


            return <NavDropdown title={item.title} id="basic-nav-dropdown" key={i}
                className={active ? "customActive1" : ""}

            >

                {item.children?.map((child, j) => {

                    if (!child.route) return

                    const active2 = location.pathname.includes(child.route)
                    console.log("child",active2, child.route)


                    return <Nav.Link
                    active={false}
                        className={active2 ? "customActive2" : ""}
                        as={Link} to={child.route} key={j * (i + 1)} style={{ color: "rgba(0,0,0,.55)", fontSize: "0.8em" }} eventKey={i + "" + j}>
                        {child.title}
                    </Nav.Link>
                })}

            </NavDropdown>
        }

    })

    return <div className={"TopNavBar"}>

        <div className={"flexTopNavBar"}>




            {/* <Container> */}
            <Navbar /* bg="light" */ expand="lg" className="navbar" collapseOnSelect>
                <Navbar.Brand >
                    <Nav.Link as={Link} to="/" eventKey={7} style={{ color: "black", padding:0, fontWeight:400 }}  >
                        {/* <Link to="/home">Home</Link> */}
                        Emotionale Unterst√ºtzung
                    </Nav.Link>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">


                        {links}


                    </Nav>
                </Navbar.Collapse>
            </Navbar>
            {/* </Container> */}

        </div>
    </div>




}