import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Link } from "react-router-dom";

import classes from "./bottomBar.module.css"
import React from 'react';


export default function BottomBar() {

    return <div className={classes.bottomBarContainerRoot}>



        <div className={classes.bottomBarContainer} >

            <Navbar  className="bottomBar">

                <Nav className="me-auto1" style={{ width: "100%" }}>

                    <div className={classes.bottomBar}>


                        <Nav.Link as={Link} to="/" >
                            {/* <Link to="/home">Home</Link> */}
                            Home
                        </Nav.Link>
                        <Nav.Link as={Link} to="kontakt"  >
                            {/* <Link to="/home">Home</Link> */}
                            Kontakt
                        </Nav.Link>
                        <Nav.Link as={Link} to="impressum" >
                            {/* <Link to="/impressum">Impressum</Link> */}
                            Impressum{/* /Haftung/Datenschutz */}
                        </Nav.Link>

                    </div>

                </Nav>

            </Navbar>



        </div>

        <div className={classes.rechte}>

            Alle Rechte vorbehalten


        </div>


    </div>



}