import React from 'react';

import { Nav } from "react-bootstrap"
import { Link } from "react-router-dom"
import { RouteString } from "../../menuStructure"

interface props {
    route?: RouteString
    click?:()=>void
    title?: string
    children?:any
    pre?:string
    post?:string
}

export default function CustomLink(props: props) {
    return <Link
        to={"/" + props.route}
        className=""
        onClick={()=> props.click && props.click()}
        style={{
            // border:"none",
            color:"transparent"
        }}
    >
        {/* {" "} */}
        {props.pre}
        <div className={"linkPhone"} style={{display:"inline", border:"", textDecoration:"underline", textDecorationThickness:1}}>

            {props.title}
            {props.children}


        </div>
        {props.post}
        {/* {" "} */}
    </Link>

}